import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Register } from '../../models/register.interface';
import { TempatLahir } from '../../models/master/tempat_lahir.interface';

import { SelectSearchableComponent } from 'ionic-select-searchable';

import { RegisterServiceProvider } from '../../providers/register-service/register-service';
import { LoadingServiceProvider } from '../../providers/loading-service/loading-service';
import { ToastServiceProvider } from '../../providers/toast-service/toast-service';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { MasterServiceProvider } from '../../providers/master-service/master-service';
import { ModalServiceProvider } from '../../providers/modal-service/modal-service';

import { Master } from '../../models/master.interface';

/**
 * Generated class for the RegisterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  register: Register = {
  	nama: '',
  	no_hp: '',
  	email: '',
    title: '',
    jenis_kelamin: '',
  	password: '',
  	password_confirmation: '',
    identitas_id:'',
    no_identitas:'',
    tgl_lahir: '',
    tempat_lahir_id:'',
  };

  list_title: Master;
  list_identitas: Master;
  list_tempat_lahir: Master;
  tempat_lahir: TempatLahir = {
    id: null,
    nama: null,
  };

  otp: boolean= false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private registerService: RegisterServiceProvider,
    private toast: ToastServiceProvider,
    private alert: AlertServiceProvider,
    private loading: LoadingServiceProvider,
    private modal: ModalServiceProvider,
    private master: MasterServiceProvider,

  ) {

   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RegisterPage');
    this.master.get_title_2().subscribe((data) => this.list_title = data.data, error => console.log(error), () => console.log('done'));
    this.master.get_identitas_2().subscribe((data) => this.list_identitas = data.data, error => console.log(error), () => console.log('done'));
    this.master.get_tempat_2().subscribe((data) => this.list_tempat_lahir = data.data, error => console.log(error), () => console.log('done'));
  }

  onRegis(){

    console.log('data',this.register)
    let loader = this.loading.show('loading ....');
    loader.present().then(() => {
      this.registerService.register(this.register).subscribe((data) => this.saveSuccess(data,loader), error => this.saveError(error,loader), () => this.saveDone());
    })
  }

  private saveDone(){
  }

  private saveError(error,l){
    l.dismiss().then(() => {
      if (error.status == 422) {
        this.alert.presentAlert(null,this.feedbackText(error.json()),'Close');
      } else if (error.status == 400) {
        this.alert.presentAlert(null,'Bad Request','Close');
      } else {
        this.toast.show('Server Error');
      }
    });
  }

  private saveSuccess(data,l){
    l.dismiss().then(() => {
       this.navCtrl.push('OtpVerificationPage');
    });
  }

  private feedbackText(error){
    let feedbackError = '<ul class="ul-item">';
    for(let key in error){
      for(let i = 0;i < error[key].length; i++){
        feedbackError += '<li class="li-item">'+error[key][i]+'</li>';
      }
    }
    feedbackError += '</ul>';

    return feedbackError;
  }

  open(){
    // let modal = this.modal.presentModal('OtpVerificationPage','');
    // modal.present();
    this.navCtrl.setRoot('LoginPage');
  }

  changeTempatLahir(event: {
      component: SelectSearchableComponent,
      value: any 
  }) {
      console.log(event)
      this.register.tempat_lahir_id = event.value.id;
  }

}



